<template>
    <div id="online-coding">
      <div class="row">
        <div class="col-md-4 pl-0" :class="menuShow? 'lession-menu pr-0' : 'lession-menu-hide'">
          <transition name="el-zoom-in-left">
          <div v-show="menuShow">
            <div class="col-md-2 lession-no"  :style="{ height: windowHeight}">
              <div class="no-dot" v-for="(lessionNo, index) in questionList.length" :key="lessionNo" :class="index === selectQIndex ? 'active-dot' : ''" @click="changeQ(index)">{{lessionNo}}</div>
            </div>
            <div class="col-md-10 lession-detail" :style="{ height: windowHeight}">
              <div class="lession-title space" v-if="questionInfo && questionInfo.title">
                {{questionInfo.title}}<i class="el-icon-arrow-left icon-shouqi" @click="changeMenu(false)"></i>
              </div>
              <div class="lession-content font-bold pb-2">【题目内容】</div>
              <div class="code-div pb-2 space pt-2 pb-2">
                <div class="code-area font-small" v-if="questionInfo">{{questionInfo.topicDetails}}
                </div>
              </div>
              <div class="lession-content font-bold pt-2 pb-2">【输入形式】</div>
              <div class="code-div pb-2">
                <div class="code-area font-small" v-if="questionInfo">{{questionInfo.input}}
                </div>
              </div>
              <div class="lession-content font-bold pt-2 pb-2">【输出形式】</div>
              <div class="code-div" style="border-bottom: 1px solid #dddddd;">
                <div class="code-area font-small" v-if="questionInfo"> {{questionInfo.output}}
                </div>
              </div>
              <div v-for="(caseInfo, caseIndex) in testCaseList" :key="caseIndex">
                <div class="lession-content font-bold pb-2">【测试用例{{caseIndex + 1}}】</div>
                <div class="code-div pb-2">
                  <div class="col-md-2 code-io pr-0  mb-3">输入：</div><div class="code-area font-small col-md-10 mb-3">{{caseInfo.in}}</div>
                  <div class="col-md-2 code-io pr-0 mb-3">输出：</div><div class="code-area font-small col-md-10 mb-3">{{caseInfo.out}}</div>
                </div>
              </div>
            </div>
          </div>
            </transition>
        </div>
        <div class="pl-0 pr-0 write-coding" :class="menuShow? 'col-md-8 col-md-offset-4' : 'col-md-12'">
          <div class="coding-header">
            <el-button class="pull-right back-btn" v-show="!menuShow" @click="changeMenu(true)">编程课程菜单</el-button>
            <span class="coding-task">{{stepName}}</span>
            <el-button class="pull-right back-btn" @click="back">返回</el-button>
          </div>
          <div class="coding-area" v-if="codemirrorShow">
            <codemirror  ref="myCm"  v-model="item.content"  :options="cmOptions"  class="code" style="border: 2px solid rgb(221, 221, 221);" ></codemirror>
            <el-tooltip content="点击切换到白天模式" placement="top" v-if="cmOptions.theme === 'monokai'">
              <i class="iconfont iconbangzhuchuangyixiangfaideadengpaofaguang theme-icon sun" v-if="cmOptions.theme === 'monokai'" @click="changeTheme('neat')"></i>
            </el-tooltip>
            <el-tooltip content="点击切换到黑夜模式" placement="top" v-if="cmOptions.theme === 'neat'">
              <i class="el-icon-moon theme-icon moon" v-if="cmOptions.theme === 'neat'" @click="changeTheme('monokai')"></i>
            </el-tooltip>
            <div class="row coding-param">
              <el-button type="success" :disabled="canSub" class="mt-3 pull-left" @click="outputCode">点我运行
                <!--<i class="el-icon-caret-right ml-1"></i>-->
              </el-button>
              <span class="font-small tip" v-if="questionInfo && questionInfo.submitId !== null">*每道题限制最多提交运行{{submitLimit}}次,当前已提交{{questionInfo.submitId}}次</span>
              <!--<el-input v-model="inputParams" placeholder="标准输入(stdin)" class="ml-4 pull-left mt-3" :style="{ width: '220px' }"></el-input>-->
              <el-button type="success" class="mt-3 pull-right" @click="getHistoryPrint()">编程历史记录
              </el-button>
            </div>
            <div class="mt-3 font-bold print-title">程序输出：</div>
            <div class="code-print" :style="{ minHeight: sizeHChange(135), paddingTop: sizeHChange(4) }">
              <!--<i class="el-icon-delete del-icon" v-if="printList.length > 0" @click="printList = []"></i>-->
              <div v-for="(printObj, printIndex) in printList" :key="printIndex" class="pt-2 pb-3">
                <span v-if="!printObj.errMsg" class="main-color">{{printObj.printCase}}<br/></span>
                <span class="font-bold">评判结果</span>：{{printObj.printText}}
                <el-tooltip placement="top">
                  <div slot="content" v-html="printObj.printDescription"></div>
                  <i class="el-icon-question main-color"></i>
                </el-tooltip>
                <br/>
                输出结果：{{printObj.outputResult}}<br/><span v-if="printObj.errMsg" style="white-space: pre-wrap;">错误信息：{{printObj.errMsg}}<br/></span>运行耗时：{{printObj.timeUsed}}ms，占用内存：{{printObj.memoryUsed}}kb。
              </div>
            </div>
          </div>
        </div>
      </div>
      <el-dialog
        title="我的编程记录"
        :visible.sync="dialogVisible"
        width="50%"
        >
        <div class="history-container" :style="{ height: sizeHChange(400) }" v-if="dialogVisible">
          <div class="mb-5 print-item" v-for="(printHistoryObj, historyIndex) in printHistoryList" :key="historyIndex">
            <div class="font-small">
              提交时间：{{printHistoryObj.time}}
            </div>
            <div class="row">
              <div class="history-code font-small col-md-6 mr-3 ml-3">{{printHistoryObj.code}}</div>
              <div class="history-print col-md-5 font-small">
                <div v-for="(result, resultIndex) in printHistoryObj.result" :key="resultIndex">
                  <span v-if="!result.errMsg" class="main-color">{{result.printCase}}<br/></span>
                  <span class="font-bold">评判结果</span>：{{result.printText}}
                  <br/>
                  输出结果：{{result.outputResult}}<br/><span v-if="result.errMsg">错误信息：{{result.errMsg}}<br/></span>运行耗时：{{result.timeUsed}}ms，占用内存：{{result.memoryUsed}}kb。
                </div>
              </div>
            </div>
          </div>
          <div v-if="printHistoryList.length === 0" class="text-center font-small" :style="{ height: sizeHChange(350), lineHeight: sizeHChange(350) }">您暂未提交过代码。</div>
        </div>
      </el-dialog>
    </div>
</template>

<script>
import { Loading } from 'element-ui'
import { codemirror } from 'vue-codemirror'
import 'codemirror/lib/codemirror.css'
// 风格
require('codemirror/theme/monokai.css') // 夜晚
require('codemirror/theme/neat.css') // 白天
// keyMap
require('codemirror/keymap/sublime')
// 编辑中的配置
require('codemirror/addon/edit/matchbrackets.js') // 匹配括号
require('codemirror/addon/edit/closebrackets.js') // 匹配括号
require('codemirror/addon/edit/matchtags.js') // 匹配标签
require('codemirror/addon/edit/closetag.js') // 匹配标签
require('codemirror/addon/edit/continuelist.js') //
// 插件-提示
require('codemirror/addon/hint/show-hint.css')
require('codemirror/addon/hint/show-hint.js') // 显示提示
require('codemirror/addon/hint/sql-hint') // sql提示
require('codemirror/addon/hint/javascript-hint') // js提示
require('codemirror/addon/hint/css-hint') // css提示
require('codemirror/addon/hint/javascript-hint') // js提示
require('codemirror/addon/hint/xml-hint') // Xml提示
require('codemirror/addon/hint/html-hint') // HTML提示
// 插件-搜索
require('codemirror/addon/search/search') // 搜索
require('codemirror/addon/search/searchcursor') // 搜索
require('codemirror/addon/search/jump-to-line') // 搜索
require('codemirror/addon/dialog/dialog') // 搜索
require('codemirror/addon/dialog/dialog.css') // 搜索

// 插件-代码展开收起
require('codemirror/addon/fold/foldcode.js')
require('codemirror/addon/fold/brace-fold.js')
require('codemirror/addon/fold/foldgutter.css')
require('codemirror/addon/fold/foldgutter.js')
require('codemirror/addon/fold/xml-fold.js')
require('codemirror/addon/fold/indent-fold.js')
require('codemirror/addon/fold/markdown-fold.js')
require('codemirror/addon/fold/comment-fold.js')

// 编程语言
require('codemirror/mode/python/python.js')
require('codemirror/mode/javascript/javascript') // mode: "javascript"
require('codemirror/mode/css/css') // mode: "text/css"
require('codemirror/mode/xml/xml') // mode: "text/html"
require('codemirror/mode/clike/clike') // mode: "text/x-java"
require('codemirror/mode/sql/sql')
// mode text/x-csrc

export default {
  components: {
    codemirror
  },
  name: 'online-coding',
  created () {
    let me = this
    me.stepId = localStorage.getItem('stepId')
    me.teamId = localStorage.getItem('teamId')
    me.userCode = localStorage.getItem('userCode')
  },
  mounted () {
    this.initDoms()
    this.initData(true)
  },
  data () {
    return {
      stepName: '编码任务',
      canSub: true,
      submitLimit: 5,
      dialogVisible: false,
      selectQIndex: 0,
      submitId: 0,
      stepId: '',
      teamId: '',
      userCode: '',
      questionList: [],
      questionInfo: null,
      item: {
        content: ''
      },
      windowHeight: '',
      curCode: '',
      cmOptions: {
        tabSize: 4,
        matchBrackets: true, // 匹配括号
        autoCloseBrackets: true, // 匹配括号
        matchTags: true, // 匹配标签
        mode: 'text/x-java',
        theme: 'neat', // 主题（默认白色）
        lineNumbers: true, // 行号
        lineWrapping: true,
        foldGutter: true,
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        extraKeys: { 'Ctrl': 'autocomplete' }, // 代码按下Ctrl提示
        lineWiseCopyCut: true,
        line: true,
        scrollbarStyle: 'null'
      },
      modeObj: { Java: 'text/x-java', C: 'text/x-csrc' },
      judgeObj: { Java: 8, C: 1 },
      judgeId: 0,
      menuShow: true,
      codemirrorShow: false,
      Jmodel: 'public class Main{\n' +
              '  public static void main(String[] args){\n' +
              '  }\n' +
              '}',
      Cmodel: '#include<stdio.h>\n' +
              'int main()\n' +
              '{\n' +
              '\n' +
              '}',
      printList: [],
      inputParams: '',
      resultStatusList: [
        { title: '答案正确', description: '恭喜你！所提交的代码通过了数据！这个评测结果应该是大家最喜欢见到的，也非常好理解。<br/>如果是单点测试，那么只要通过了，就会返回一个“答案正确”；<br/>如果是多点测试，那么只有当通过了所有数据时，才会返回“答案正确”。' },
        { title: '格式错误', description: '这应该是最接近正确答案的错误了，<br/>基本上由多输出了空格或者换行导致的，稍作修改即可。' },
        { title: '运行超时', description: '由于每道题都会规定程序运行时间的上限，因此当超过这个限制时就会返回“运行超时”。<br/>一般来说，这一结果可能是由算法的时间复杂度过大而导致的，当然也可能时某组数据使得代码中某处地方死循环掉了。<br/>因此，要仔细思考最坏时间复杂度是多少，或者检查代码中是否可能数显特殊数据死循环的情况。' },
        { title: '内存超限', description: '每道题目都有规定程序使用的空间上限，因此如果程序中使用太多的空间，<br/>则会返回MLE，例如数组太大一般最容易导致这个结果。' },
        { title: '答案错误', description: '“答案错误”是比较令人懊恼的结果，因为这说明代码有漏洞或者算法根本就是错误的，只是恰好能过样例而已。<br/>不过有时可能时应为输出了一些调试信息导致的，那就删除多余的输入内容再输出。<br/>当然，大部分情况下都需要认真检测代码逻辑有没有问题。' },
        { title: '运行错误', description: '这一结果的可能性非常多，常见的有段错误（直接的原因时非法访问了内存，例如数组越界，指针乱指），<br/>浮点错误（例如除数为0，模数为0），递归爆栈（一般由递归时层数过深导致的）等。<br/>一般来说，需要先检查数组大小是否比题目的数据范围大，然后再去检查可不可能有特殊数据可以使除数或者模数为0，有递归的情况则检查是否在大数据时递归层数太深。' },
        { title: '输出超限', description: '如果程序输出了过量的内容（一般是指过量非常多），那么就会返回“输出超限”。<br/>一般是由输出了大量的调试信息或者特殊数据导致的死循环输出导致的。' },
        { title: '编译错误', description: '很显然，如果代码没有办法通过编译，那么就会返回“编译错误”。<br/>这是要先注意时不是选错了语言，然后再看本地的编译器能不能编译通过刚刚提交的代码，修改轴再次提交即可。' },
        { title: '系统错误', description: '-_-!系统出现异常，请联系工作人员！' }
      ],
      testCaseList: [ { in: '100 50 10', out: '47977.93' }, { in: '80 100 120', out: '341595.37' } ],
      printHistoryList: [{ time: '2019-10-10 12:00:22',
        code: '#include<stdio.h>\n' +
          'int main()\n' +
          '{\n' +
          ' int a,b,i,a1=0,b1=0;\n' +
          ' double c=0,c1=0,sum=0;\n' +
          ' scanf("%d",&a);\n' +
          ' scanf("%d",&b);\n' +
          ' scanf("%lf",&c);\n' +
          ' a1=((1+a)*a)/2;\n' +
          ' for(i=1;i<=b;i++)\n' +
          ' b1+=i*i;\n' +
          ' for(i=1;i<=c;i++)\n' +
          ' {\n' +
          '  c1+=1.0/i;\n' +
          ' }\n' +
          ' sum=a1+b1+c1;\n' +
          ' printf("%0.2lf",sum);\n' +
          ' return 0;\n' +
          '}',
        result: [{ printCase: '测试用例1', printText: '答案正确', outputResult: '47797.93', timeUsed: '0', memoryUsed: '888' }] }]
    }
  },
  methods: {
    initDoms () {
      this.windowHeight = window.innerHeight + 'px'
    },
    initData (setCm) {
      let me = this
      me.stepName = localStorage.getItem('stepName')
      me.$http.post('/doproject/questionLibraryInfo', { 'stepId': me.stepId, 'teamId': me.teamId }, resQ => {
        if (resQ.success) {
          // resJudge.data = resJudge.data[0]
          me.questionList = resQ.data
          me.questionInfo = resQ.data[me.selectQIndex]
          me.questionInfo.sampleInputArr = me.questionInfo.sampleInput.split(',')
          me.questionInfo.sampleOutputArr = me.questionInfo.sampleOutput.split(',')
          me.testCaseList = []
          for (var i = 0; i < me.questionInfo.sampleInputArr.length; i++) {
            let caseObj = { in: me.questionInfo.sampleInputArr[i], out: me.questionInfo.sampleOutputArr[i] }
            me.testCaseList.push(caseObj)
          }
          if ((me.questionInfo.submitId + 1) > me.submitLimit) {
            me.canSub = true
          } else {
            me.canSub = false
          }
          if (setCm) {
            me.setCodeMirror()
          }
        } else {
          // 返回错误的处理
        }
      })
      me.codemirrorShow = true
    },
    changeTheme (theme) {
      this.$set(this.cmOptions, 'theme', theme)
    },
    outputCode () {
      let me = this
      let data = {
        'problemId': me.questionInfo.id,
        'input': me.questionInfo.sampleInputArr,
        'output': me.questionInfo.sampleOutputArr,
        'timeLimit': me.questionInfo.timeLimit,
        'memoryLimit': me.questionInfo.memoryLimit,
        'judgeId': me.judgeId, // C:1,2,3 Java:8
        'src': me.item.content,
        'callBack': 'http://www.aimaonline.cn:8086/producer/result.do?key=111&submitId=' + (me.questionInfo.submitId + 1),
        'userCode': me.userCode,
        'teamId': me.teamId
      }
      me.$axios({
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        url: 'http://www.aimaonline.cn:8086/producer/judge.do',
        data: data,
        dataType: 'json',
        processData: false,
        contentType: false,
        timeout: 60000
      }).then((res) => {
        if (res.data === 'OK') {
          const loading = Loading.service({
            lock: true,
            text: '程序运行中...',
            spinner: 'el-icon-loading',
            background: 'rgba(f, 0, 0, 0.7)',
            color: '#6612cf'
          })
          me.printList = []
          setTimeout(function () {
            let judgeData = {
              'problemId': me.questionInfo.id,
              'teamId': me.teamId
            }
            me.$http.post('/doproject/judgeResultInfo', judgeData, resJudge => {
              if (resJudge.success) {
                // resJudge.data = resJudge.data[0]
                for (var i = 0; i < resJudge.data.length; i++) {
                  if (resJudge.data[i].errorMessage) {
                    let obj = { printCase: '', printText: '运行错误', printDescription: '-_-!您的代码存在错误，请根据下方错误信息进行修改！', memoryUsed: 0, timeUsed: 0, outputResult: 'null', errMsg: resJudge.data[i].errorMessage }
                    me.printList.push(obj)
                  } else {
                    let obj = { printCase: '测试用例' + (i + 1), printText: me.resultStatusList[resJudge.data[i].resultStatus].title, printDescription: me.resultStatusList[resJudge.data[i].resultStatus].description, memoryUsed: resJudge.data[i].memoryUsed, timeUsed: resJudge.data[i].timeUsed, outputResult: resJudge.data[i].outputResult, errMsg: null }
                    me.printList.push(obj)
                  }
                }
                me.initData(false)
                loading.close()
              } else {
                // 返回错误的处理
                loading.close()
              }
            })
          }, 4000)
        }
      })
    },
    getHistoryPrint () {
      let me = this
      me.printHistoryList = []
      let judgeData = {
        'problemId': me.questionInfo.id,
        'teamId': me.teamId
      }
      me.$http.post('/doproject/judgeResultHistoryInfo', judgeData, resHistory => {
        if (resHistory.success) {
          for (var i = 0; i < resHistory.data.length; i++) {
            let judgearr = resHistory.data[i].judgeResultDetail
            let historyObj = { result: [] }
            for (var j = 0; j < judgearr.length; j++) {
              historyObj.time = judgearr[j].createTime
              historyObj.code = judgearr[j].codeContent
              if (judgearr[j].errorMessage) {
                let obj = { printCase: '', printText: '运行错误', printDescription: '-_-!您的代码存在错误，请根据下方错误信息进行修改！', memoryUsed: 0, timeUsed: 0, outputResult: 'null', errMsg: judgearr[j].errorMessage }
                historyObj.result.push(obj)
              } else {
                let obj = { printCase: '测试用例' + (j + 1), printText: me.resultStatusList[judgearr[j].resultStatus].title, printDescription: me.resultStatusList[judgearr[j].resultStatus].description, memoryUsed: judgearr[j].memoryUsed, timeUsed: judgearr[j].timeUsed, outputResult: judgearr[j].outputResult, errMsg: null }
                historyObj.result.push(obj)
              }
            }
            me.printHistoryList.push(historyObj)
          }
        } else {
          // 返回错误的处理
        }
      })
      me.dialogVisible = true
    },
    setCodeMirror () {
      let me = this
      me.cmOptions.mode = me.modeObj[me.questionInfo.judgeName]
      if (me.cmOptions.mode === me.modeObj.Java) {
        me.item.content = me.Jmodel
        me.judgeId = me.judgeObj.Java
      } else if (me.cmOptions.mode === me.modeObj.C) {
        me.item.content = me.Cmodel
        me.judgeId = me.judgeObj.C
      }
    },
    changeMenu (status) {
      this.menuShow = status
    },
    changeQ (index) {
      this.selectQIndex = index
      this.initData(true)
      this.printList = []
    },
    sizeHChange (size) {
      return window.innerHeight * size / 739 + 'px'
    },
    back () {
      window.location = 'task-list.html'
    }
  }
}
</script>

<style scoped>

</style>
